@charset "utf-8";
/*////////////////////////////////////////////////////

	汎用レイアウト（レイアウトセット参照）
		ここではレイアウトに関わるスタイルのみ記載し、
		背景色や背景画像、ボーダー、シャドウなどのデザインバリエーションは、
		_pattern.scss内に継承クラスで指定すること
		（例：.wrapper01.style01）

////////////////////////////////////////////////////*/
/* ------------------------------------------------
	.boxFlexContentシリーズここから
------------------------------------------------ */

.box-flex-content {
	@include flexbox;
	@include flex-direction;
	@include flex-align;
	&.reverse {
		@include flex-direction(row-reverse);
	}
	&.type01 {
		@include flex-align($align: start);
		&>.box-img{
			width:34.8%;
			@include media(sp){
				width: 100%;
			}
		}
		&>.box-content{
			width:calc(100%);
			@include media(sp){
				width: 100%;
			}
		}
	}
	&.type02{
		// @include flex-align($align: start);
		&>.box-img{
			width:34.8%;
			@include media(sp){
				width: 100%;
			}
		}
		&>.box-content{
			width:65.2%;
			@include media(sp){
				width: 100%;
			}
		}
	}
}


.row-2col{
	@include flexbox;
	@include flex-direction($direction:row);
	@include flex-justify-content($justify:space-between);
	@include flex-align($align: center);
}


