.flexrows{
	@include flexbox;
	@include flex-direction($direction:row);
	@include flex-justify-content($justify:space-between);
	@include box-sizing;
	&.rows2{
		&>.box-content {
			width:calc(100%/2);
			@include media(sp){
				width:calc(100%/2 - 2%);
			}
		}
	}
	&.rows3{
		&>.box-content {
			width:calc(100%/3);
			@include media(sp){
				width:calc(100%/2 - 2%);
			}
		}
	}
	&.rows4{
		&>.box-content {
			width:calc(100%/4);
			@include media(sp){
				width:calc(100%/2 - 2%);

			}
		}
	}
	&.rows3type01{
		&>.box-content {
			width:calc(100%);
		}
	}
}

/* ------------------------------------------------
	.gridシリーズここから
------------------------------------------------ */

.l-grid{
	display:grid;
	grid-template-columns:1fr 1fr ;
	gap:70px 40px;
	@include media(tablet){
		gap:60px 30px;
		grid-template-columns:1fr 1fr;
	}
	@include media(sp){
		grid-template-columns:1fr;
	}
	&.type01{
		grid-template-columns:1fr 1fr 1fr 1fr;
	}
}

.base-glid{
	display:grid;
	grid-template-rows:auto;
	width:100%;
	gap:15px 15px;
	grid-template-columns:1fr 1fr 1fr;
	@include media(sp){
		grid-template-columns:1fr 1fr;
		gap:10px 10px;
	}
	&.type01{
		grid-template-columns:1fr 1fr 1fr 1fr 1fr;
		gap:3px 3px;
	}
}
